import React, { ReactNode } from "react";
import { useModalStore } from "../../../../../../../store/potg/modal";

const Modal = ({ content }: { content: ReactNode }) => {
  const { isView, setVisiblity } = useModalStore();

  return (
    <div
      className={`fixed w-full h-full top-0 left-0 z-50 select-none ${
        isView ? "" : "hidden"
      }`}
    >
      <div
        id="modalBackground"
        className="bg-black opacity-40 w-full h-full top-0 left-0"
        onClick={() => {
          setVisiblity(false);
        }}
      />
      <div
        id="modalContent"
        className="fixed w-fit h-fit top-2/4 left-2/4 bg-gray-50 shadow-2xl -translate-x-2/4 -translate-y-2/4 rounded-2xl p-7"
      >
        {content}
      </div>
    </div>
  );
};

export default Modal;
